let ct_log = {
    //
    logcheck(vm, to) {
        if (vm.$store.getters.GETme || to.path == "/loginpage") {} else {
            alert("");
            vm.$router.push("/loginpage");
        }
    },

    //
    admincheck(vm, next) {
        if (vm.$store.getters.GETme != null && vm.$store.getters.GETme.lv != "administrator") {
            vm.$router.push("/");
        }
    }

}


export default ct_log;